header {
  height: 140vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ==============CTA STYLE ============= */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ==============HEADER SOCIALS STYLE ============= */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

/* ==============ME STYLE ============= */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 10rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

img {
  display: block;
  max-height: auto;
  max-width: 100%;
  object-fit: cover;
  margin-left: auto;
  margin-right: auto;
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* media queries for medium devices tablet */

@media screen and (max-width: 1024px) {
  header {
    height: 135vh;
  }
}

/* media queries for small devices tablet */

@media screen and (max-width: 600px) {
  header {
    height: 135vh;
  }

  .header__socials,
  .scroll__down {
    display: none;
  }
}
